#section06 .small-title {
  text-align: center;
}

.carousel {
  width: 100%;
  display: grid;
  grid-template-columns: minmax(20px, 50px) minmax(200px, auto) minmax(20px, 50px);
  gap: 0 0.5em;
  place-items: center;
}

.carousel-inner {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  width: 100%;
  height: 254px;
  overflow: hidden;
  scroll-behavior: smooth;
  overflow-scrolling: auto;
  align-items: baseline;
  justify-content: flex-start;
}

.carousel-item {
  display: flex;
  height: 254px;
  flex-direction: column;
}

.carousel-item a {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 100%;
}

.carousel-item-link {
  color: black;
}

.carousel-item-headshot {
  width: 100%;
  max-width: 150px;
  height: auto;
  max-height: 150px;
  display: block;
  margin: 0 auto;
  border-radius: 100%;
}

.carousel-item-content {
  display: flex;
  flex-direction: column;
  padding: 0.5em;
}

.carousel-controls {
  display: flex;
  width: 100%;
  place-content: center;
  background-color: transparent;
  border: 0;
  outline: none;
  padding: 0.5em 0;
}

.carousel-controls-icon {
  display: inline-block;
  border-top: 1rem solid transparent;
  border-bottom: 1rem solid transparent;
  width: 0;
  height: 0;
  opacity: 0.9;
  transition: opacity 0.15s ease;
  margin: 0 auto;
}

.carousel-controls-icon--prev {
  border-right: 1.25rem solid #1f82c5;
}

.carousel-controls-icon--next {
  border-left: 1.25rem solid #1f82c5;
}

.carousel-controls:hover .carousel-controls-icon {
  opacity: 1;
}

.carousel-controls:focus {
  outline: 5px auto #1f82c5 !important;
}
